.container .wrapper.realestate-hero {
    background: linear-gradient(180deg, rgba(216, 101, 48, 0.195) 0%, rgba(255, 255, 255, 0) 108.18%);
    padding-bottom: 4rem;
    overflow: hidden;
    height: 1000px;
}


/* @media only screen and (min-width:768px) {
    .container .wrapper.realestate-hero {
        padding-bottom: 6rem;
    }
} */

.realstate-hero-background::before {
    /* min-width: 1400px; */
    position: absolute;
    content: "";
    top: 250px;
    opacity: 0.7;
    overflow: hidden;
    width: 100%;
    left: 0rem;
    height: 600px;
    width: 100%;
    z-index: 3;
    background: url('../../../Assets/Images/city-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.realstate-hero-background::after {
    /* min-width: 1400px; */
    position: absolute;
    width: 100%;
    left: 0%;
    height: 600px;
    top: 350px;
    content: "";
    z-index: 2;
    background: url('../../../Assets/Images/city-bg.png');
    opacity: 0.1;
    transform: matrix(1, 0, 0, -1, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.realestate-data-container {
    margin-top: -425px;
    position: relative;
    z-index: 5;
}

@media only screen and (min-width : 768px) {
    .realestate-hero::after {
        top: 300px;
    }

    .realestate-hero::before {
        top: 200px;
    }

    .container .wrapper.realestate-hero {
        height: 800px;
    }

    .realestate-data-container {
        margin-top: -500px;
    }
}

.realestate-hero .services-tabs {
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
    position: relative;
    z-index: 4;
}

@media only screen and (min-width:768px) {
    .realestate-hero .services-tabs {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media only screen and (min-width:1024px) {
    .realestate-hero .services-tabs {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

.realestate-hero .services-tabs li {
    width: 100%;
    background-color: #fff;
    padding: 1rem;
    border-radius: 1rem;
    padding: 1rem;
    box-shadow: 4px 4px 24px rgba(0, 0, 0, 0.08);
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    align-items: center;
}

.realestate-hero .services-tabs li h3 {
    font-size: 20px;
    line-height: 20px;
    color: #666666;
    font-weight: 500;
}

.realestate-hero .services-tabs li:first-child h3 {
    color: #AB4618;
}

.realestate-hero .services-tabs li:first-child div {
    background: url('../../../Assets//Images/Realestate.svg');
    width: 64px;
    height: 64px;
}

.realestate-hero .services-tabs li:nth-child(2) div {
    background: url('../../../Assets//Images/financialGrey.svg');
    width: 64px;
    height: 64px;
}

.realestate-hero .services-tabs li:last-child div {
    background: url('../../../Assets//Images/medicalGrey.svg');
    width: 64px;
    height: 64px;
}

.realstate-search {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    margin-top: 3rem;
    z-index: 4;
    position: relative;
}

.realstate-search h2 {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #272727;
}

.realstate-search .search-bar {
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
}

.realstate-search .search-bar .search-bar-inputs {
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.04), 0px 4px 16px rgba(0, 0, 0, 0.05);
    border-radius: 100px;
}

.realstate-search .search-bar .search-bar-inputs select {
    background-color: #fff;
    height: 48px;
    border-radius: 100px 0px 0px 100px;
    padding: 0rem 32px 0px 32px;
    font-size: 14px;
    font-weight: 500;
    color: #272727;
    -moz-appearance: none;
    -webkit-appearance: none;
    min-width: 80px;
}

.realestate-hero .search-bar .search-bar-inputs .location-select select {
    border-radius: 0px 100px 100px 0px;
    border-left: 1px solid #e1e4e5;
    color: #9496A0;
}

.realestate-hero .search-bar .search-bar-inputs .search-select {
    position: relative;
}

.realestate-hero .search-bar .search-bar-inputs .search-select .down-icon {
    position: absolute;
    right: 12px;
    top: 50%;
    color: #272727;
    transform: translateY(-50%);
}

.realestate-hero .search-bar .search-bar-inputs .search-select .location-icon {
    position: absolute;
    left: 10px;
    top: 50%;
    color: #272727;
    transform: translateY(-50%);
}

@media only screen and (min-width: 768px) {
    .realstate-search .search-bar {
        background-color: #fff;
        padding: 1px;
        box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.04), 0px 4px 16px rgba(0, 0, 0, 0.05);
        flex-direction: row;
        gap: 0px;
        border-radius: 100px;
        overflow: hidden;
    }

    .realstate-search .search-bar .search-bar-inputs {
        box-shadow: none;
        border-radius: 0px;
        align-items: center;
        background-color: #fff;
    }

    .realstate-search .search-bar .search-bar-inputs select {
        padding: 0rem 3rem;
    }

    .realstate-search .search-bar button {
        margin-left: 6rem;
    }
}

.realestate-top .header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
    margin-bottom: 1rem;
}

.realestate-top .header h3 {
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    color: #0F1C49;
}

.realestate-top .header a {
    color: #0084D1;
    font-size: 14px;
    font-weight: 500;
}

.realestate-top .content-wrapper ul {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

@media only screen and (min-width: 551px) {
    .realestate-top .content-wrapper ul {
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (min-width: 768px) {
    .realestate-top .content-wrapper ul {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media only screen and (min-width: 1024px) {
    .realestate-top .content-wrapper ul {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}

.realestate-top .content-wrapper ul li {
    background-color: #fff;
    box-shadow: 0px 4px 20px rgba(158, 158, 158, 0.2);
    border-radius: 1rem;
}

.realestate-top .content-wrapper ul li div {
    height: 150px;
}

.realestate-top .content-wrapper ul li div img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1rem 1rem 0px 0px;
}

.realestate-top .content-wrapper ul li h4 {
    padding: 8px 1rem 0px 1rem;
    font-size: 14px;
    line-height: 32px;
    font-weight: 400;
    color: #0F1C49
}

.realestate-top .content-wrapper ul li p {
    color: #878DA4;
    font-size: 12px;
    padding: 0px 1rem 8px 1rem;
}

.realestate-works {
    background: radial-gradient(98.8% 98.8% at 50% 95.69%, #F9F6F5 0%, #F9F6F5 51.56%, rgba(249, 246, 245, 0) 100%);
}

.realestate-works .details {
    display: grid;
    gap: 6rem;
    grid-template-columns: 1fr;
}

.realestate-works .details .details-section {
    display: grid;
    gap: 2rem;
    grid-template-columns: 1fr;
}

.realestate-works .details .details-section:first-child .section-image {
    background: url("../../../Assets/Images/Illustration4.jpeg");
    height: 300px;
    background-repeat: no-repeat;
    background-position: center;
}

.realestate-works .details .details-section:last-child .section-image {
    background: url("../../../Assets/Images/Illustration5.jpeg");
    height: 300px;
    background-repeat: no-repeat;
    background-position: center;
}

.realestate-works .details .details-section .content {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.realestate-works .details .details-section .content h2 {
    font-weight: 400;
    font-size: 32px;
    color: #0F1C49;
}

.realestate-works .details .details-section .content h2 span {
    color: #E07F51;
}

.realestate-works .details .details-section .content ul {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding-left: 2rem;
}

.realestate-works .details .details-section .content li p {
    color: #878DA4;
    font-weight: 400;
    font-size: 18px;
}

.realestate-works .details .details-section .content li {
    position: relative;
}

.realestate-works .details .details-section .content li svg {
    position: absolute;
    left: -24px;
    top: 5px;
    background: rgba(124, 213, 24, 0.2);
    color: #88D92F;
    /* border: 2px solid #7CD518; */
    width: 16px;
    background-color: #E3F3CE;
    height: 16px;
    border-radius: 50%;
}

.realestate-works .details .details-section .content button {
    padding: 11px 16px;
    display: flex;
    align-items: center;
    gap: 16px;
}

@media only screen and (min-width :768px) {

    .realestate-works .details .details-section {
        grid-template-columns: 1fr 1fr;
    }
}

.realestate-results .content-wrapper {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.realestate-results .filters-section h2 {
    font-weight: 400;
    font-size: 20px;
    color: #000;
    margin-bottom: 1.5rem;
}

.realestate-results .filters-section {
    width: 100%;
    max-width: 250px;
}

.realestate-results .filters-section .filters {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.realestate-results .filters-section .filters .filter {
    margin-bottom: 0rem;
}

.realestate-results .filters-section .filters .filter .header {
    display: flex;
    align-items: center;
    gap: 4px;
    flex-wrap: wrap;
    padding-bottom: 4px;
    border-bottom: 1px solid rgba(233, 235, 240, 0.99);
}

.realestate-results .filters-section .filters .filter .header h4 {
    font-size: 14px;
    color: #000;
    font-weight: 400;
    flex: 1;
}

.realestate-results .filters-section .filters .filter .header button {
    border-radius: 0px;
    padding: 0px;
    background-color: transparent;
    color: #9496A0;
    font-size: 11px;
}

.realestate-results .filters-section .filters .filter .header .toggle-btn {
    margin-top: 4px;
    transition: 0.1s all linear;
}

.realestate-results .filters-section .filters .filter ul {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 1rem;
    overflow: hidden;
    height: 0px;
    transition: 0.1s all linear;
}

.realestate-results .filters-section .filters .filter.active ul {
    height: 100%;
}

.realestate-results .filters-section .filters .filter.active .header .toggle-btn {
    transform: rotate(180deg);
    margin-top: -4px;
}

.realestate-results .filters-section .filters .filter ul li label {
    display: flex;
    gap: 8px;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
    color: #000;
}

.realestate-results .filters-section .filters .filter ul li label span {
    color: #9496A0;
}

.realestate-results .filters-section .filters .filter ul li label input[type='radio'] {
    padding: 0px;
    width: auto;
}

.realestate-results .all-properties-section .sort {
    display: flex;
    gap: 8px;
    align-items: center;
    margin-bottom: 2rem;
}

.realestate-results .all-properties-section .sort svg {
    color: #626573;
}


.realestate-results .all-properties-section .sort span,
.realestate-results .all-properties-section .sort select {
    font-size: 14px;
    color: #090041;
    font-weight: 400;
    background-color: transparent;
}

.realestate-results .all-properties-section .sort span {
    font-weight: 500;
}

.realestate-results .all-properties-section .properties .list {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.realestate-results .all-properties-section .properties .list .property {
    background-color: #fff;
    border-radius: 1rem;
    box-shadow: 0px 4px 20px rgba(158, 158, 158, 0.2);
    display: flex;
    flex-direction: column;
}

.realestate-results .all-properties-section .properties .list .property .property-image {
    height: 150px;
}

.realestate-results .all-properties-section .properties .list .property .property-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1rem 1rem 0rem 0rem;
}

.realestate-results .all-properties-section .properties .list .property .property-details {
    padding: 1rem;
}

.realestate-results .all-properties-section .properties .list .property .property-details h3 {
    font-size: 14px;
    font-weight: 500;
    padding-bottom: 8px;
    color: #000;
}

.realestate-results .all-properties-section .properties .list .property .property-details ul {
    display: flex;
    gap: 0px 1rem;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 4px;
}

.realestate-results .all-properties-section .properties .list .property .property-details ul li span,
.realestate-results .all-properties-section .properties .list .property .property-details ul li a {
    font-size: 12px;
    font-weight: 400;
    color: #0084D1;
    line-height: 18px;
    text-decoration: underline;
}

.realestate-results .all-properties-section .properties .list .property .property-details ul li p {
    color: #878DA4;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    padding-top: 4px;
}

.realestate-results .all-properties-section .properties .list .property .property-details ul li {
    position: relative;
}

.realestate-results .all-properties-section .properties .list .property .property-details ul li:first-child::after,
.realestate-results .all-properties-section .properties .list .property .property-details ul li:nth-child(2)::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -8px;
    transform: translateY(-50%);
    width: 3px;
    height: 3px;
    background-color: #c4c4c4;
}


.realestate-results .all-properties-section .properties .list .property .property-details h4 {
    font-size: 11px;
    color: #000;
    font-weight: 400;
    margin-bottom: 1rem;
}

.realestate-results .all-properties-section .properties .list .property .property-details p {
    font-size: 11px;
    color: #000;
    font-weight: 400;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    /* number of lines to show */
    line-clamp: 4;
    -webkit-box-orient: vertical;
}


.realestate-results .all-properties-section .properties .list .property .property-details button {
    padding: 11px 1rem;
    display: block;
    margin: 1rem 0rem 0rem auto;
}


@media only screen and (min-width :768px) {
    .realestate-results .content-wrapper {
        flex-direction: row;
        gap: 4rem;
    }

    .realestate-results .filters-section {
        max-width: 200px;
        width: 100%;
    }

    .realestate-results .all-properties-section .sort {
        justify-content: flex-end;
    }

    .realestate-results .all-properties-section .properties .list .property {
        flex-direction: row;
    }

    .realestate-results .all-properties-section .properties .list .property .property-image {
        height: 250px;
        max-width: 300px;
        width: 100%;
    }

    .realestate-results .all-properties-section .properties .list .property .property-image img {
        border-radius: 1rem 0rem 0rem 1rem;
        height: 100%;
        width: 100%;
    }
}