.navbar {
    position: fixed;
    top: 0rem;
    left: 0rem;
    z-index: 999;
    background-color: #fff;
    width: 100vw;
    overflow: hidden;
    height: 80px;
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content: space-between;
}

.navbar .logo {
    flex: 1;
}

.navbar .logo h1 {
    font-size: 36px;
    font-weight: 700;
    color: #0084D1;
}

.notification-btn svg {
    color: #0084D1;
}

.notification-btn {
    width: max-content;
    position: relative;
}

.notification-btn span {
    position: absolute;
    top: -0.5rem;
    right: -0.5rem;
    background-color: #FF5C83;
    font-weight: 400;
    color: #fff;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
}

.navbar ul {
    position: fixed;
    width: 100%;
    max-width: 450px;
    top: 0rem;
    right: -450px;
    z-index: 1000;
    background-color: #fff;
    height: 100vh;
    box-sizing: border-box;
    padding: 8rem 3rem;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 2rem;
    transition: 0.3s all linear;
}

.navbar .active-menu {
    right: 0px;
}

.navbar ul li:first-child {
    position: absolute;
    top: 1rem;
    right: 1rem;
}

.navbar ul li a {
    color: #272727;
    font-weight: 600;
    font-size: 20px;
}

.navbar ul li:nth-child(7) {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
}

.navbar ul li:nth-child(7) span {
    display: flex;
    gap: 4px;
    align-items: center;
}

.navbar ul li:nth-child(7) svg {
    color: #0084D1;
}

.navbar ul li:nth-child(7) span {
    font-size: 20px;
    font-weight: 600;
    color: #272727;
}

.navbar ul li:nth-child(8) button {
    padding: 11px 16px;
}

@media only screen and (min-width : 768px) {
    .navbar ul {
        max-width: 350px;
        right: -350px;
    }
}

@media only screen and (min-width : 1024px) {
    .navbar {
        padding: 1rem 4rem;
        gap: 4rem;
    }

    .navbar ul {
        position: static;
        background-color: transparent;
        box-shadow: none;
        flex-direction: row;
        right: auto;
        top: auto;
        padding: 0rem;
        height: auto;
        width: 100%;
        max-width: 100%;
        align-items: center;
        gap: 1.5rem;
    }

    .navbar ul li:first-child {
        display: none;
    }

    .navbar ul li a {
        font-size: 16px;
    }

    .navbar ul li:nth-child(7) {
        flex-direction: row;
    }

    .navbar ul li:nth-child(7) span {
        font-size: 16px;
        margin-right: 4rem;
    }

    .nav-btn {
        display: none;
    }

    .nav-btn.notification-btn {
        display: block;
        position: absolute;
        top: 50%;
        right: 16rem;
        z-index: 1000;
        transform: translateY(-50%);
    }
}


.admin-header {
    padding: 1rem 2rem;
    background-color: #F0F2F5;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 1rem;
    position: relative;
}

.admin-header button {
    width: auto;
    height: auto;
    padding: 0rem;
    background-color: transparent;
    border-radius: 0rem;
}

.admin-header button svg {
    color: #7B809A;
}

.admin-header .sidebar {
    transition: 0.1s all linear;
    position: fixed;
    top: 1rem;
    left: -266px;
    z-index: 999;
    height: calc(100vh - 2rem);
    background: linear-gradient(195deg, #42424a, #191919);
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 10%), 0 2px 4px -1px rgb(0 0 0 / 6%);
    border-radius: 10px;
    width: 250px;
}

.admin-header .sidebar.active {
    left: 1rem;
}

.admin-header .sidebar .header {
    padding: 2rem 1rem;
    text-align: center;
    position: relative;
}

.admin-header .sidebar .header p {
    color: #fff;
    line-height: 16px;
}

.admin-header .sidebar .header .close-btn {
    position: absolute;
    right: 1rem;
    top: 1rem;
}

.admin-header .sidebar .header .close-btn svg {
    color: #9F9FA3;
}

.admin-header .sidebar .tabs {
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 4px;
    overflow-y: auto;
    max-height: calc(100vh - 112px);
}

.admin-header .sidebar .tabs .link {
    padding: 14px 1rem;
}

.admin-header .sidebar .tabs .link a {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #fff;
    font-weight: 300;
    font-size: 0.875rem;
}

.admin-header .sidebar .tabs .link.active {
    background-color: #E3306F;
    border-radius: 6px;
}



@media only screen and (min-width: 1024px) {
    .admin-header .sidebar {
        left: 1rem;
    }

    .admin-header .sidebar .header .close-btn,
    .admin-header .open-btn {
        display: none;
    }
}