body,
input,
button,
select,
option,
textarea,
nav,
ul,
li,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span,
p,
q,
div,
aside,
section,
label,
table,
td,
th,
tbody,
thead,
tr {
    font-family: 'Poppins', sans-serif;
    padding: 0%;
    margin: 0%;
    border: 0px;
    box-sizing: border-box;
    list-style: none;
    outline-style: none;
    -webkit-tap-highlight-color: transparent;
    text-decoration: none;
    border-collapse: collapse;
}

body {
    width: 100%;
    overflow-x: hidden;
}

button {
    padding: 11px 36px;
    border-radius: 48px;
    background-color: #0084D1;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    line-height: 20px;
    letter-spacing: 1px;
    width: max-content;
    cursor: pointer;
}

.nav-btn {
    padding: 0rem;
    background-color: transparent;
    color: #272727;
}

label {
    font-size: 12px;
    line-height: 14px;
    color: #626573;
    border-radius: 32px;
    font-weight: 500;
}

input {
    width: 100%;
    background-color: #fff;
    padding: 20px 24px 16px 24px;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
    border-radius: 32px;
    transition: 0.1s all linear;
}

input[type="checkbox"] {
    width: auto;
    padding: 0%;
}

::placeholder {
    color: #626573;
}

.error-message {
    margin: 0px !important;
    padding: 0rem !important;
    font-size: 13px !important;
    line-height: 13px !important;
    font-weight: 400 !important;
    letter-spacing: 0.5px;
    color: #C82333 !important;
}

.input-box {
    position: relative;
}


.input-box label {
    position: absolute;
    left: 24px;
    top: 4px;
    display: none;
}

.input-box svg {
    color: #272727;
    position: absolute;
    right: 16px;
    top: 50%;
    transform: translateY(-50%);
}

.input-box:focus-within label,
.input-box.active label {
    display: block;
}

.input-group {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

@media only screen and (min-width: 1024px) {
    .input-group {
        grid-template-columns: 1fr 1fr;
    }
}

.checkbox-container {
    display: block;
    position: relative;
    padding-left: 24px;
    cursor: pointer;
    font-size: 14px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.checkbox-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: -2px;
    left: 0;
    height: 18px;
    width: 18px;
    background-color: #fff;
    border-radius: 3px;
}

.checkbox-container.success .checkmark {
    background-color: #eee;
    border: 1px solid #878DA4;
}

/* On mouse-over, add a grey background color */
.checkbox-container:hover input~.checkmark {
    background-color: #eee;
}

/* When the checkbox is checked, add a blue background */
.checkbox-container input:checked~.checkmark {
    background-color: #0084D1;
}

.checkbox-container.success input:checked~.checkmark {
    background-color: #fff;
    border: 0px;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.checkbox-container input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.checkbox-container .checkmark:after {
    left: 6px;
    top: 2px;
    width: 3px;
    height: 10px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.checkbox-container.success .checkmark:after {
    border-color: #00A860;
    border-width: 0 4px 4px 0;
}

.help-block {
    width: 100%;
    margin-top: 0.25rem;
    font-size: 80%;
    color: #dc3545;
}

.container {
    margin-top: 80px;
    box-sizing: border-box;
}

.container .wrapper {
    padding: 4rem 2rem;
    box-sizing: border-box;
    position: relative;
}

.container .wrapper.wrapper-border-top {
    border-top: 1px solid rgba(233, 235, 240, 0.99);
}

.container .wrapper .wrapper-header {
    position: absolute;
    background-color: #fff;
    font-size: 14px;
    font-weight: 500;
    padding: 8px 16px;
    border: 1px solid rgba(233, 235, 240, 0.99);
    border-radius: 2rem;
    top: -1rem;
    left: 50%;
    transform: translateX(-50%);
    line-height: 14px;
}

.container .wrapper .content-wrapper {
    max-width: 1100px;
    margin: 0rem auto;
    box-sizing: border-box;
}

@media only screen and (min-width:768px) {
    .container .wrapper {
        padding: 4rem;
    }
}





/* ADMIN */

.admin-container {
    padding: 3rem 2rem 2rem 2rem;
    background-color: #F0F2F5;
    transition: 0.1s all linear;
    min-height: calc(100vh - 53px);
}

.admin-container .breadcrumbs p {
    font-weight: 300;
    color: #929FB4;
    font-size: 13px;
}

.admin-container .breadcrumbs p b {
    font-weight: 400;
    color: #344767;
}

.admin-container .breadcrumbs span {
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #344767;
}

.admin-container .breadcrumbs {
    position: absolute;
    top: 1rem;
}

@media only screen and (min-width: 1024px) {
    .admin-container {
        padding-left: 298px;
    }
}

.admin-container .content-wrapper .table-view {
    background-color: #fff;
    border-radius: 1rem;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
    border: 0 solid rgba(0, 0, 0, .125);
    position: relative;
    padding-top: 6rem;
    margin-top: 2rem;
}

.admin-container .content-wrapper .table-view .header {
    background: linear-gradient(195deg, #ec407a, #d81b60);
    border-radius: 12px;
    padding: 1rem;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, .14), 0 7px 10px -5px rgba(233, 30, 99, .4);
    width: calc(100% - 2rem);
    display: flex;
    gap: 1rem;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: absolute;
    left: 1rem;
    top: -37px;
}

.admin-container .content-wrapper .table-view .header h1 {
    font-size: 1rem;
    font-weight: 600;
    line-height: 1.625rem;
    color: #fff;
}

.admin-container .content-wrapper .table-view .table {
    overflow-x: auto;
}

.admin-container .content-wrapper .table-view .table table {
    align-items: center;
    width: 100%;
}

.admin-container .content-wrapper .table-view .table thead th {
    padding: 12px 24px;
    text-align: left;
    border-bottom: 1px solid #f0f2f5;
    color: #7b809a;
    opacity: 0.7;
    font-size: 12px;
    font-weight: 500;
}

.admin-container .content-wrapper .table-view .table tbody td {
    text-align: left;
    padding: 12px 24px;
    border-bottom: 1px solid #f0f2f5;
}

.admin-container .content-wrapper .table-view .table tbody td img {
    border-radius: 8px;
    height: 36px;
    width: 36px;
    object-fit: cover;
}

.admin-container .content-wrapper .table-view .table tbody tr:last-child td {
    border-bottom: 0px;
}

.admin-container .content-wrapper .table-view .table tbody td {
    color: #7b809a;
    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
}

.admin-container .content-wrapper .table-view .table tbody .head {
    font-size: 0.875rem;
    color: #344767;
}

.admin-container .content-wrapper .table-view .table .table-actions {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 8px;
    align-items: center;
}

.admin-container .content-wrapper .table-view button {
    padding: 5px 10px;
    border-radius: 3px;
    font-size: 12px;
    font-weight: 500;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.admin-container .content-wrapper .table-view .table .status {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
    border: 1px solid rgba(255, 255, 255, 0.5);
}

.admin-container .content-wrapper .table-view .table .status.active {
    background: linear-gradient(195deg, #90ee90, #20a520);
}

.admin-container .content-wrapper .table-view .table .status.deactive {
    background: linear-gradient(195deg, #ff1818, #ff545c);
}

.admin-container .content-wrapper .table-view .add-btn {
    padding: 8px 10px;
    color: #212529;
    background-color: #f8f9fa;
    font-size: 13px;
}

.admin-container .content-wrapper .table-view .add-btn:hover {
    background-color: #e2e6ea;
}

.admin-container .content-wrapper .table-view .deactivate-btn {
    /* background-color: #6c757d; */
    background: linear-gradient(195deg, #747b8a, #495361);
}

.admin-container .content-wrapper .table-view .edit-btn {
    background: linear-gradient(195deg, #36bfd4, #17a2b8);
}

.admin-container .content-wrapper .table-view .activate-btn {
    background: linear-gradient(195deg, #66bb6a, #43a047);
}


.modal-container {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0rem;
    left: 0rem;
    height: 100vh;
    width: 100vw;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-container .modal {
    margin-top: 1.5rem;
    width: calc(100% - 2rem);
    max-width: 700px;
    max-height: calc(100vh - 5rem);
    background-color: rgba(255, 255, 255, 0.9);
    padding: 1rem;
    border-radius: 1rem;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
    position: relative;
    padding-top: 4rem;
}

.modal-container .modal .header {
    background: linear-gradient(195deg, #ec407a, #d81b60);
    border-radius: 12px;
    padding: 1rem;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, .14), 0 7px 10px -5px rgba(233, 30, 99, .4);
    width: calc(100% - 2rem);
    display: flex;
    gap: 1rem;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: absolute;
    left: 1rem;
    top: -28px;
}

.modal-container .modal .header h3 {
    color: #fff;
    line-height: 1.625rem;
    font-size: 16px;
    font-weight: 600;
}

.modal-container .modal .header button {
    padding: 0rem;
    background-color: transparent;
    border-radius: 0px;
}

.modal-container .modal .content {
    max-height: calc(100vh - 10rem);
    overflow-y: auto;
}

.modal-container .modal .content form {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 8px;
}

.modal-container .modal .content::-webkit-scrollbar {
    display: none;
}

.modal-container .modal .content form .modal-input-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 24px;
}

@media only screen and (min-width: 768px) {
    .modal-container .modal .content form .modal-input-grid {
        grid-template-columns: 1fr 1fr;
    }
}

.modal-container .modal .content form .modal-input-box {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.modal-container .modal .content form .modal-input-box input {
    border-radius: 0rem;
    padding: 8px;
    background: transparent;
    border-bottom: 1px solid rgba(0, 0, 0, .15);
    transition: 0.1s all linear;
}

.modal-container .modal .content form .modal-input-box:focus-within label {
    color: #0084D1;
}

.modal-container .modal .content form .modal-input-box:focus-within input {
    border-bottom: 2px solid #0084D1;
}

.modal-container .modal .content form .action-box {
    display: flex;
    align-items: center;
    gap: 16px;
    justify-content: flex-end;
}

.modal-container .modal .content form .action-box button {
    padding: 5px 10px;
    border-radius: 3px;
    font-size: 12px;
    font-weight: 500;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.modal-container .modal .content form .action-box .create {
    background: linear-gradient(195deg, #66bb6a, #43a047);
}

.modal-container .modal .content form .image-box {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.modal-container .modal .content form .image-box .image-wrapper {
    width: 150px;
    height: 150px;
    border: 1px solid rgba(0, 0, 0, .15);
}

.modal-container .modal .content form .image-box .image-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.modal-container .modal .content form .image-box label,
.modal-container .modal .content form .image-box .remove {
    width: 100px;
    text-align: center;
    letter-spacing: 0px;
    padding: 8px 10px;
    border-radius: 3px;
    background-color: #344767;
    line-height: 14px;
    color: #fff;
    font-size: 12px;
    font-weight: 500;
    box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.1), 0px 2px 4px -1px rgba(0, 0, 0, 0.06);
    cursor: pointer;
}

.modal-container .modal .content form .image-box .remove {
    background: linear-gradient(195deg, #C82333, #cc0b1e);
}

.modal-container .modal .content form .image-box .image-box-actions {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.modal-container .modal .content form .image-box input {
    display: none;
}