.container .wrapper.landing-hero {
    background: linear-gradient(180deg, #E0F2FF 12.38%, #FFFFFF 99.97%);
    overflow: hidden;
    padding-bottom: 4rem;
}


@media only screen and (min-width:768px) {
    .container .wrapper.landing-hero {
        padding-bottom: 6rem;
    }
}

.landing-hero::before {
    /* min-width: 1400px; */
    position: absolute;
    content: "";
    top: 350px;
    overflow: hidden;
    width: 100%;
    left: 0rem;
    opacity: 0.7;
    height: 600px;
    width: 100%;
    z-index: 3;
    background: url('../../Assets/Images/city-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

.landing-hero::after {
    /* min-width: 1400px; */
    position: absolute;
    width: 100%;
    left: 0%;
    height: 600px;
    top: 530px;
    content: "";
    z-index: 2;
    background: url('../../Assets/Images/city-bg.png');
    opacity: 0.1;
    transform: matrix(1, 0, 0, -1, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 600px;
}

@media only screen and (min-width : 768px) {
    .landing-hero::after {
        top: 280px;
    }

    .landing-hero::before {
        top: 100px;
    }
}

.landing-hero .content-wrapper {
    position: relative;
    z-index: 10;
}

.landing-hero-header {
    display: flex;
    align-items: center;
    gap: 0px 10px;
    flex-wrap: wrap;
    margin-bottom: 2rem;
}

.landing-hero-header h1 {
    font-weight: 700;
    font-size: 36px;
    color: #272727;
}

.landing-hero-header ul {
    height: 60px;
    line-height: 60px;
    overflow: hidden;
    text-align: center;
}

.landing-hero-header ul li {
    position: relative;
    height: 90px;
    top: 0;
    animation: slide 4.5s steps(3) infinite;
}

.landing-hero-header ul li::before {
    position: absolute;
    content: "";
    width: 100%;
    left: 0;
    top: 2rem;
    height: 15px;
    background-color: #fff
}

@keyframes slide {
    100% {
        top: -270px;
    }
}

.landing-hero-header ul li:first-child h1 {
    color: #D09B6B;
    position: relative;
}

.landing-hero-header ul li:nth-child(2) h1 {
    color: #8227C3;
    position: relative;
}

.landing-hero-header ul li:nth-child(3) h1 {
    color: #57BAC4;
    position: relative;
}

.landing-hero p {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 2rem;
}

.landing-hero-enquire {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}

.landing-hero-enquire input {
    max-width: 375px;
    height: 42px;
    padding: 0rem 32px;
}

.landing-hero-enquire button {
    height: 42px;
    padding: 0rem 24px;

}

.landing-hero-services {
