.container .wrapper .medical-hero {
    background: linear-gradient(180deg, #57BAC4 0%, rgba(255, 255, 255, 0) 108.18%);
    /* opacity: 0.5; */
}

.medical-hero::before {
    /* min-width: 1400px; */
    position: absolute;
    content: "";
    bottom: 225px;
    overflow: hidden;
    width: 100%;
    left: 0rem;
    height: 400px;
    width: 100%;
    z-index: 3;
    background: url('../../../Assets/Images/city-bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 400px;
}

.medical-hero::after {
    /* min-width: 1400px; */
    position: absolute;
    width: 100%;
    left: 0%;
    height: 400px;
    bottom: 165px;
    content: "";
    z-index: 2;
    background: url('../../../Assets/Images/city-bg.png');
    opacity: 0.1;
    transform: matrix(1, 0, 0, -1, 0, 0);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 400px;
}
